import './App.css';
import React, { Component } from 'react';
import AlimentEditor from './components/aliment-editor/AlimentEditor';
import AlimentModel from './models/AlimentModel';
import AlimentList from './components/AlimentList/AlimentList';
import StorageProcessorFactory from './StorageProcessorFactory';
import ConsumptionModel from './models/ConsumptionModel';
import IdGenerator from './IdGenerator';
import * as common from './common';

class App extends Component {

  constructor(props) {
    super(props);
    this.idGenerator = new IdGenerator();
    this.alimentSP = new StorageProcessorFactory.getAlimentSP();
    this.consumptionSP = new StorageProcessorFactory.getConsumptionSP();
    this.state = {
      aliment: new AlimentModel('Poire'),
      aliments: this.alimentSP.findAll(),
      consumedAliments: this.consumptionSP.findAll(),
    }
  }

  /**
   * adding a new aliment to the list
   * @param {Aliment} aliment 
   */
  addAlimentToList = aliment => {
    this.alimentSP.addToList(aliment);
    const aliments = this.alimentSP.findAll();
    this.setState({ aliments });
  }

  consumeAliment = alimentId => {
    const consumption = new ConsumptionModel(
      this.idGenerator.generateConsumptionId(),
      alimentId,
      new Date(Date.now())
    );
    this.consumptionSP.addToList(consumption);
    const consumedAliments = this.consumptionSP.findAll();
    this.setState({ consumedAliments });
  }

  render() {
    return (
      <div className="container-fluid">
        <h1>App Title</h1>
        <br />
        {/* ajouter un aliment */}
        <AlimentEditor aliment={this.state.aliment} onSubmit={this.addAlimentToList} />
        <br /><br />
        {/* lister les aliments*/}
        <AlimentList aliments={this.state.aliments} onSelected={this.consumeAliment} />
      </div>
    );
  }
}

export default App;



